.stream {
    /* flex: 0.4; */
    border-right: 1px solid var(--main-background);
    /* min-width: fit-content; */
    /* width: 70em; */
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    overflow-y: scroll;
    margin-top: 2em;
    width: 100%;
  }

  /* Hide scrollbar for Chrome, Safari and Opera */ 
  .feed::-webkit-scrollbar {
    display: none;
  }
  
/* Hide scrollbar for IE, Edge and Firefox */
.feed {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; 
}/* Firefox */

/* .xyz {
  visibility: hidden;
} */

.stream__posts {
  margin: 1em;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.stream__posts > .post {
  visibility: visible;
  width: 100%;
}

.stream__header > h2 {
  font-size: 20px;
  font-weight: 800;
}
